@import url(https://fonts.googleapis.com/css?family=Asap:400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, left top, right top, from(#1c0059), color-stop(92%, #3b0054), to(#3b0054));
  background: -webkit-linear-gradient(left, #1c0059 0%, #3b0054 92%, #3b0054 100%);
  background: linear-gradient(to right, #1c0059 0%, #3b0054 92%, #3b0054 100%);
  overflow: auto; }

/* header */
header {
  background: #ead7ff;
  padding: 10px 20px; }
  header h1 {
    color: #33005e;
    text-align: center;
    font-family: Asap, sans-serif;
    font-size: 40px;
    font-weight: bold; }
  header p {
    color: #1f263b;
    font-family: Asap, sans-serif;
    text-align: center;
    padding: 0;
    margin: 0; }

#mybtn {
  border-radius: 5%;
  background-color: #33005e;
  border: none;
  font-size: 14px;
  position: relative;
  margin-bottom: 10px; }

.rb {
  background-color: #ead7ff;
  border-radius: 15%;
  color: #1f263b;
  position: relative;
  top: 50px;
  left: 5px;
  width: 60px;
  font-size: 14px;
  font-family: sans-serif;
  font-weight: bold;
  box-shadow: 7px 7px 5px 0px rgba(0, 0, 0, 0.75); }

/* score and choices */
.score-board {
  margin: 20px auto;
  border: 3px solid white;
  width: 200px;
  color: white;
  font-size: 40px;
  border-radius: 4px;
  text-align: center;
  padding: 15px 20px;
  font-family: sans-serif;
  position: relative; }

.badge {
  background-color: #ff5dbb;
  color: #ead7ff;
  font-size: 14px;
  padding: 2px 10px;
  font-family: sans-serif; }

#user-label {
  position: absolute;
  top: 30px;
  left: -20px; }

#comp-label {
  position: absolute;
  top: 30px;
  right: -30px; }

.result {
  font-size: 40px;
  font-family: sans-serif;
  color: #ead7ff; }
  .result p {
    text-align: center;
    font-weight: bold; }

.choice {
  display: inline-block;
  border: 4px solid white;
  border-radius: 50%;
  padding: 10px;
  margin: 0 20px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.choice:hover {
  cursor: pointer;
  background: #080c16; }

#Fire:hover {
  background: #4e0000; }

#Water:hover {
  background: #000a36; }

#Grass:hover {
  background: #001f05; }

.choice img {
  width: 100px;
  max-height: 100px; }

.choices {
  text-align: center;
  margin: 50px 0; }

#action-message {
  text-align: center;
  color: #ead7ff;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 20px; }

/*footer*/
.jumbotron {
  background-color: #ead7ff;
  min-height: 80px;
  padding: 0px;
  margin: 0px;
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 14px; }

.foot {
  margin-bottom: 0px; }

.insta {
  max-width: 40px;
  padding-left: 10px; }

.fb {
  max-width: 38px; }

/* classes */
.green-glow {
  border: 4px solid #4dcc7d;
  box-shadow: 0 0 10px #31b43a; }

.red-glow {
  border: 4px solid #f35757;
  box-shadow: 0 0 10px #ff0909; }

.yellow-glow {
  border: 4px solid #fdff76;
  box-shadow: 0 0 10px #ffc400; }

.red-font {
  color: #ff0909; }

/* media queries */
@media screen and (max-height: 690px) {
  .result p {
    font-size: 16px; }
  .choice img {
    width: 30px;
    max-height: 30px; }
  .score-board span {
    font-size: 30px; }
  header h1 {
    font-size: 28px; }
    header h1 p {
      font-size: 18px;
      margin: 0px;
      padding: 0px; }
  .jumbotron {
    position: relative; }
  #mybtn {
    font-size: 10px; } }

@media only screen and (max-width: 608px) {
  .choice img {
    width: 50px;
    max-height: 50px; }
  #action-message {
    text-align: center;
    color: #ead7ff;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 16px; }
  .result p {
    font-size: 30px;
    padding: 20px; }
  .rb {
    width: 50px; }
  #mybtn {
    font-size: 10px; } }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

